@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bangers&display=swap);
.zoom {
  transition: all 0.2s ease-in-out;
}
.zoom:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

h5 {
  font-family: "Roboto", sans-serif;
}

.card {
    margin: 0 auto; /* Added */
    float: none; /* Added */
    margin-bottom: 10px; /* Added */
}
form {
  padding-top: 100px;
  padding-left: 200px;
  padding-right: 200px;
}

.search-box {
  margin-top: 50px;
}

.field-container {
  padding: 0;
  margin: 0;
  border: 0;
  width: 400px;
  height: 60px;
}

.field {
  border: 0;
  width: 100%;
  height: 100%;
  padding: 10px 20px;
  background: white;
  border-radius: 3px;
  box-shadow: 0px 8px 15px rgba(75, 72, 72, 0.1);
  transition: all 0.4s ease;
}
.field:focus {
  outline: none;
  box-shadow: 0px 9px 20px rgba(75, 72, 72, 0.3);
}
.field:focus + .icons-container .icon-close {
  opacity: 1;
  -webkit-transform: translateX(0);
          transform: translateX(0);
}
.field:focus + .icons-container .icon-search {
  opacity: 0;
  -webkit-transform: translateX(200%);
          transform: translateX(200%);
}

@media (max-width: 770px) {
  .search-box {
    margin-top: 100px;
    margin-bottom: 50px;
    margin-left: 45px;
    margin-right: 45px;
  }
}

.center {
  padding-top: 100px;
  display: flex;
  justify-content: center;
  height: 100vh;
}

.not {
  color: red;
  padding: 50px 0 0 0;
  text-align: center;
  font-weight: 600;
  font-family: "Montserrat", sans-serif;
}

.height {
  height: 100vh;
}

.fluid-container {
  margin: 100px;
}
.bot {
  padding-left: 50px;
}
h2 {
  font-family: "Ubuntu", sans-serif;
  font-weight: 500;
  font-size: 50px;
}
h3 {
  font-size: medium;
}
h4 {
  font-size: larger;
  font-weight: 100;
  font-style: italic;
}
h1 {
  font-size: 30px;
}
.space {
  margin-left: 0px;
  text-align: center;
  align-items: center;
}
.row .center-img {
  text-align: center;
  align-items: center;
}
.containter .plot {
  width: 75%;
}

@media (max-width: 970px) {
  .containter .plot {
    text-align: center;
    align-items: center;
    width: 100%;
    padding: 0 40px 0 40px;
  }
  img,
  .cent {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 50%;
  }

  .bot {
    padding-left: 0px;
    padding-bottom: 50px;
    text-align: center;
  }
  .fluid-container {
    margin: 10px;
  }
  .start {
    padding-left: 0;
    text-align: center;
    padding-bottom: 10px;
  }
}

strong {
  font-family: "Bangers", cursive;
  font-size: 25px;
  letter-spacing: 3px;
}

div#main {
  color: #102a42;
  background-color: #f1f5f8;
}

html,
body {
  width: 100%;
  margin: 0px;
  padding: 0px;
  overflow-x: hidden;
  background-color: #f1f5f8;
}

